body {
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  margin: 0;
  height: 100vh;
  display: flex;
  gap: 12px;
  background: #f7f7fb;
}

.app {
  display: flex;
  gap: 12px;
  padding: 12px;
  width: 100%;
  box-sizing: border-box;
  flex-direction: column;
}

.left {
  width: 320px;
  background: white;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 4px 14px rgba(10,10,20,0.06);
}

.right {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.panel {
  background: white;
  border-radius: 8px;
  padding: 12px;
  box-shadow: 0 4px 12px rgba(10,10,20,0.04);
}

.control-row {
  display: flex;
  gap: 8px;
  align-items: center;
  margin-bottom: 8px;
}

.interactive{
  /* position: relative; */
  display:flex;
  flex-direction: row;
  gap: 12px;
}
.static_part {
  /* position: relative; */
  display:flex;
  flex-direction: row;
  gap: 12px;
  height: 400px;
}
.control-row label { width: 64px; }
.select, input[type="number"], input[type="range"] { flex: 1; }
.small {
  width: 72px;
}
.canvas {
  height: 420px;
}
.plot {
  height: 260px;
}
.footer {
  overflow: auto;
  padding: 8px;
}

.Plot5SH {
  display: None;
  flex-direction: column;
}

.shs {
  display: flex;
  flex-direction: row;
  height: 80px;
}
.shi {
  width: 80px;
  width: 80px;
}


@media (max-width: 768px) {
  body {
    height: auto;
    min-height: 100vh;
    gap: 8px;
  }

  .app {
    padding: 8px;
    gap: 8px;
  }

  .interactive, .static_part {
    flex-direction: column;
  }

  .left {
    width: 100%;
    margin-bottom: 8px;
  }

  .right {
    width: 100%;
  }

  .panel {
    padding: 8px;
  }

  .canvas {
    height: 300px;
  }

  /* Show the SH gallery on mobile */
  .Plot5SH {
    display: none;
    flex-direction: column;
    gap: 8px;
    margin-top: 16px;
  }

  .shs {
    flex-wrap: wrap;
    justify-content: center;
    height: auto;
    gap: 8px;
    margin-bottom: 8px;
  }

  .shi {
    width: calc(50% - 4px);
    height: 120px;
  }
}

/* Tablet styles (769px to 1024px) */
@media (min-width: 769px) and (max-width: 1024px) {
  .interactive {
    flex-direction: column;
  }

  .left {
    width: 100%;
  }

  .canvas {
    height: 350px;
  }

  .Plot5SH {
    display: flex;
  }

  .shs {
    justify-content: center;
  }

  .shi {
    width: 80px;
    height: 80px;
  }
}

@media (min-width: 1025px) {
  .Plot5SH {
    display: flex;
    flex-direction: column;
    gap: 12px;
    margin-top: 20px;
  }

  .shs {
    justify-content: center;
    height: 100px;
    gap: 12px;
  }

  .shi {
    width: 80px;
    height: 80px;
  }
}